<template>
  <div>
    <header class="header">
      <div class="title">{{headerTitle}}</div>
    </header>
  </div>
</template>

<script type="text/ecmascript-6">
import { mapState } from "vuex";
export default {
  name: "v-header",
  data() {
    return {};
  },
  computed: {
    ...mapState(["headerTitle"])
  },
  methods: {
    hideDetail() {
      this.detailShow = false;
    }
  }
};
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
@import '../../common/css/mixin.styl';

.header {
  position: fixed;
  top: 0px;
  width:100%;
  text-align: center;
  height: .3rem;
  background-color: #fff798;
  box-shadow: 0 0 2px rgba(150, 150, 150, 0.3);
  z-index: 21;

  .title {
    font-size: .12rem;
    line-height: .3rem;
    color: #a2733d;
    letter-spacing 0.01rem;
    position relative;
    top 1px;
  }
}
</style>



