<template>
  <div class="login-page">
      <!-- <v-header></v-header> -->
      <div class="input-section">
          <div class="cell phone-cell">
              <span class="cell-name">手机号</span>
              <input @textInput="textInutCheck('0',$event)" class="phone-text" type="number" ref="phoneText" @focus="phoneTextFocusHandle" @blur="phoneTextBlurHandle" v-model="phoneTextValue" placeholder="请输入手机号" >
              <div v-show="showClear" ref="clearBtn" @click="clearTextHandle" class="text-clear-div"><span class="iconfont icon-close"></span></div>
          </div>
          <div class="cell code-cell">
              <span class="cell-name">验证码</span>
              <input @textInput="textInutCheck('1',$event)" class="code-text" type="number" ref="codeText" v-model="codeTextValue" placeholder="请输入验证码" >
              <mt-button size="small" class="code-btn" type="default" @click.native="getCodeHandle">{{codeBtnWord}}</mt-button>
          </div>
      </div>
      <mt-button size="large" class="login-btn" type="default" @click.native="loginHandle">下一步</mt-button>
      <div class="img-container"><img onclick="return false" class="img" src="../../assets/aml_logo.png"/></div>
  </div>
</template>

<script>
import md5 from "js-md5";
import { mapState } from "vuex";
import vHeader from "../header/header.vue";
import Vue from "vue";
import { Toast, Button } from "mint-ui";
import "mint-ui/lib/button/style.css";
import "mint-ui/lib/toast/style.css";
Vue.component(Button.name, Button);
Vue.component(Toast.name, Toast);
import { savaToLocal, loadFromlLocal } from "../../common/js/storage.js"; // 公共方法：本地缓存

export default {
  name: "v-login",
  data() {
    return {
      phoneTextValue: "",
      codeTextValue: "",
      codeBtnWord: "获取验证码",
      showClear: false,
      intervalId: -1000,
      leftSeconds: 0,
      codeBtn: null
    };
  },
  components: { vHeader },
  activated() {
    this.codeTextValue = "";
  },
  mounted() {},
  beforeRouteLeave(to, from, next) {
    next();
  },
  watch: {
    phoneTextValue(val) {
      if (!val) this.showClear = false;
    }
  },
  methods: {
    textInutCheck(type, event) {
      if (!/^[0-9]*$/.test(event.data)) {
        event.returnValue = false;
      }
      if (type == "0") {
        if (this.phoneTextValue.length >= 11) event.returnValue = false;
        else this.showClear = true;
      } else {
        if (this.codeTextValue.length >= 4) event.returnValue = false;
      }
    },
    phoneTextFocusHandle() {
      if (this.phoneTextValue) this.showClear = true;
    },
    phoneTextBlurHandle() {
      this.showClear = false;
    },
    clearTextHandle(event) {
      this.phoneTextValue = "";
      this.$refs.phoneText.focus();
    },
    checkPhoneNum() {
      if (!this.phoneTextValue) {
        Toast({
          message: "请输入手机号码",
          iconClass: "iconfont icon-tishi1",
          duration: 1500
        });
        return false;
      } else {
        if (this.phoneTextValue.length < 11) {
          Toast({
            message: "请输入有效的手机号码",
            iconClass: "iconfont icon-tishi1",
            duration: 1500
          });
          return false;
        } else {
          var phnum = this.phoneTextValue.slice(0, 11);
          if (!/^1[34578]\d{9}$/.test(phnum)) {
            Toast({
              message: "请输入有效的手机号码",
              iconClass: "iconfont icon-tishi1",
              duration: 1500
            });
            return false;
          }
        }
      }
      return true;
    },
    checkCodeNum() {
      if (!this.codeTextValue) {
        Toast({
          message: "请输入验证码",
          iconClass: "iconfont icon-tishi1",
          duration: 1500
        });
        return false;
      } else if (this.codeTextValue.length != 4) {
        Toast({
          message: "验证码输入有误",
          iconClass: "iconfont icon-tishi1",
          duration: 1500
        });
        return false;
      }
      return true;
    },
    countSeconds: function() {
      this.leftSeconds = 60;
      this.codeBtnWord = "剩余" + this.leftSeconds + "s";
      if (!this.codeBtn)
        this.codeBtn = document.querySelector(".code-cell .code-btn");
      this.codeBtn.classList.add("count");
      this.intervalId = window.setInterval(() => {
        this.leftSeconds--;
        this.codeBtnWord = "剩余" + this.leftSeconds + "s";
        if (this.leftSeconds > 0) return;
        this.clearCodeInterval();
      }, 1000);
    },
    clearCodeInterval() {
      if (this.intervalId == -1000) return;
      window.clearInterval(this.intervalId);
      this.codeBtn.classList.remove("count");
      this.codeBtnWord = "获取验证码";
      this.intervalId = -1000;
    },
    getCodeHandle() {
      if (!this.checkPhoneNum()) return;
      this.$http
        .post(this.path + "sms/suncode/msgservice.php", {
          type: "loginmsg",
          phone: this.phoneTextValue
        })
        .then(response => {
          var respdata = response.body;
          if (respdata.Code == "OK") {
            this.countSeconds();
          } else if (respdata.Code == "isv.BUSINESS_LIMIT_CONTROL") {
            Toast({
              message: "操作太频繁",
              iconClass: "iconfont icon-tishi1",
              duration: 1500
            });
          } else if (respdata == "102") {
            Toast({
              message: "该手机号码已绑定",
              iconClass: "iconfont icon-tishi1",
              duration: 1500
            });
          } else {
            Toast({
              message: "验证码发送失败",
              iconClass: "iconfont icon-tishi1",
              duration: 1500
            });
          }
        })
        .catch(response => {
          //请求出错处理
          console.log(response);
        });
    },
    loginHandle() {
      if (!this.checkPhoneNum()) return;
      if (!this.checkCodeNum()) return;
      let wxid = window.getCookie("amxl_wxopenid");
      this.$store.dispatch("toggleLoading", true);
      this.$http
        .post(this.path + "php/Route.php", {
          type: "login",
          phone: this.phoneTextValue,
          code: this.codeTextValue,
          wxopenid: wxid
        })
        .then(response => {
          var irv = response.body.error;
          if (irv == "ok") {
            this.clearCodeInterval();
            let openid = response.body.openid;
            let key = response.body.key;
            let userid = response.body.userid;
            let token = md5(openid + key);
            savaToLocal("aml", "openid", openid);
            savaToLocal("aml", "userid", userid);
            savaToLocal("aml", "token", token);
            this.$store.commit("UPDATE_PHONENO", this.phoneTextValue);
            this.$store.commit("UPDATA_TOKEN", token);
            this.$store.commit("UPDATE_OPNEID", openid);
            this.$store.commit("UPDATE_USERID", userid);
            this.updatateUserInfo(this.loginedJumpPage);
          } else if (irv == "201") {
            this.$store.dispatch("toggleLoading", false);
            Toast({
              message: "验证码错误",
              iconClass: "iconfont icon-tishi1",
              duration: 1500
            });
          } else {
            this.$store.dispatch("toggleLoading", false);
            Toast({
              message: "验证码无效",
              iconClass: "iconfont icon-tishi1",
              duration: 1500
            });
          }
        })
        .catch(response => {
          this.$store.dispatch("toggleLoading", false);
          console.log(response);
        });
    },
    loginedJumpPage() {               //登录之后跳转
      if(this.role=='1'||this.role=='6'){
        if(!this.bindStoreInfo){
          Toast({
            message: "登录成功，请绑定店铺",
            duration: 3000
          });
          this.$router.replace({ path: "/bindStore" });
          return;
        }
      }
      this.$router.go(-1);
    }
  },
  computed: {
    ...mapState(["openid", "path", "userid", "role",'bindStoreInfo'])
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.login-page {
  font-family: 'Microsoft YaHei';
  // background-color: #eee;
  display: flex;
  flex-direction: column;

  .input-section {
    margin-top: 0.24rem;
    padding: 0 0.14rem;
    box-sizing: border-box;

    .cell {
      margin-top: 0.12rem;
      width: 100%;
      height: 0.38rem;
      border-bottom: 1px #ddd solid;
      display: flex;
      align-items: center;
      position: relative;

      .cell-name {
        flex-shrink: 0;
        font-size: 0.12rem;
        color: #000;
        letter-spacing: 0.01rem;
      }

      input {
        flex: 1;
        width: 100%;
        -webkit-appearance: none; // 去掉iphone背景阴影
        border: none;
        outline: none;
        box-sizing: border-box;
        -webkit-touch-callout: none;
        color: #333;
        padding-left: 0.28rem;
        font-size: 0.14rem;
        position: relative;
        top: -0.01rem;
        letter-spacing: 0.005rem;

        &::-webkit-input-placeholder {
          color: #ddd;
          font-size: 0.12rem;
          letter-spacing: 0.01rem;
        }
      }

      .text-clear-div {
        position: absolute;
        right: 0px;
        top: 50%;
        transform: translate(-50%, -50%);

        span {
          font-size: 0.16rem;
          color: #8a8a8a;
        }
      }
    }

    .code-cell {
      .code-btn {
        flex: 0 0 0.88rem;
        background-color: #d9d9d9;
        color: #222;
        font-size: 0.12rem;
        height: 0.28rem;
        box-shadow: none;
        border-radius: 0;
        letter-spacing: 0.005rem;

        &.count {
          font-size: 0.12rem;
          pointer-events: none;
          color: #666;
          background-color: #e3e3e3;
        }
      }
    }
  }

  .login-btn {
    background-color: #f8ef94;
    color: #595656;
    margin-top: 0.34rem;
    margin-left: 0.25rem;
    width: calc(100% - 0.5rem);
    font-size: 0.12rem;
    letter-spacing: 0.02rem;
    height: 0.32rem;
    box-shadow: none;
  }

  .img-container {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;

    .img {
      width: 1.5rem;
      height: auto;
    }
  }
}
</style>



